import Vue from 'vue'
import VueRouter from 'vue-router'
// for setting VueRouter
import ItemDetails from '../views/ItemDetails'
import Buy from '../views/Buy'
import Cards from '../views/Cards'
import Register  from '../views/Register.vue'
import Login from "../views/Login.vue"
import Orders from '../views/Admin/Orders.vue'
import UserCart from '@/views/User/UserCart.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '',
    component: Cards
  },
  {
    path: '/login',
    component: Login,
    name: 'Login',
    // children: [
      
    // ],
    
  },
  {
    path: '/register',
    component: Register,
    beforeEnter: ((to, from, next) => {
      if (from.name !== 'Login'){
        next({ name: 'Login'})
      }else next()
    }),
  },
  {
    path: '/buy',
    component: Buy,
    
  },
  {
    path: "/item/:id",
    component: ItemDetails
  },
  {
    path: '/orders',
    name: 'Orders',
    component: Orders,
    beforeEnter: ((to, from, next) =>{
      const status = localStorage.getItem("adminLogin")
      if (!status) {
        next({ path: '/' })
      } else 
        next()
    }) 
  },
  {
    path: '/usercart',
    name: 'UserCart',
    component: UserCart,
    beforeEnter: ((to, from, next) =>{
      const status = localStorage.getItem("userLogin")
      if (!status) {
        next({ path: '/' })
      } else 
        next()
    }) 
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
