<template>
  <div class="col-sm-12 col-md-12 col-lg-12">
    <OrderCard :orders="orders" :noOrders="noOrders" />
  </div>
</template>

<script>
import OrderCard from "../../components/adminUi/OrderCard";
export default {
  components: { OrderCard },
  data() {
    return {
      noOrders: false,
    };
  },
  computed: {
    status() {
      return this.$store.getters.getStatus;
    },
    orders() {
      if (this.status.stat !== true) {
        console.log("Order: ", this.status.stat);
        this.$router.push({ path: "/" });
      } else {
        var orders = this.$store.getters.getOrders;
        if (orders.length != 0) {
          console.log("Orders: ", orders);
          return orders;
        } else {
          this.noOrders = true;
        }
      }
    },
  },
};
</script>

<style></style>
